import React, { useState } from "react";
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';

const CreateNote =(props)=>{
    const [expand,setExpand] = useState(false)
    const [note,setnote] = useState({
        title:"",
        content:"",
    }) 
    const Inputevent=(event)=>{
const {name,value} = event.target;
setnote((prevalue)=>{
return {
...prevalue ,[name]:value  } 
})
    }
    const addEvent =()=>{
props.passNote(note);
setnote({title :"",
content :"",})
    }
    const expandit = ()=>{
        setExpand(true);
    }
    const btonormal = ()=>{
        setExpand(false);
    }

    return<><div className ="main"   onDoubleClick ={btonormal}>
    <form>
        {expand ?<input type = "text"  name ="title" value ={note.title}
         onChange ={Inputevent}
          placeholder =" Title" autoComplete="off"/>: null}

        <textarea rows="" column="" name ="content" value ={note.content} 
        onChange ={Inputevent} 
        placeholder="Write a note..." 
        onClick ={expandit}
      ></textarea>

      { expand? <Button onClick ={addEvent}>
        <AddIcon className ="plus-sign"/> 
        </Button>: null}
        </form>
        </div>
    </>
      
}
export default CreateNote;